import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';

type ProductItemProps = {
    key: number,
    product: any
}

const ProductItem: React.FC<ProductItemProps> = (props) => {
    return (
        <TableRow key={props.key}>
            <IdCell>{props.product.id}</IdCell>
            <TableCell>{props.product.billing.first_name} {props.product.billing.last_name} </TableCell>
            <TableCell>{props.product.total}€</TableCell>
            <TableCell>{props.product.status}</TableCell>
            <TableCell>{props.product.shipping_lines[0].method_title}</TableCell>
        </TableRow>
    );
}

const IdCell: React.FC = (props) => {
    return (
        <TableCell>{props.children}</TableCell>
    );
}

export default ProductItem;